body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
 

.left-hand-cursor {
  cursor: url('cursors/leftHand.cur') 0 6, default;
}

.open-hand-cursor {
  cursor: url('cursors/openHand.cur') 8 8, default;
}

.point-hand-cursor {
  cursor: url('cursors/pointHand.cur') 6 0, default;
}

.right-hand-cursor {
  cursor: url('cursors/rightHand.cur') 15 6, default;
}



